import React, { Component } from "react"
import _t from "../../services/i18n.service"
import Form from "react-validation/build/form";
import Input from "react-validation/build/input";
import Select from "react-validation/build/select";
import CheckButton from "react-validation/build/button";
import BackendService from "../../services/backend.service"
import Classroom from "./classroom.component.js"

class Student extends Component {
    
    constructor(props) {

        super(props);

        this.onChangeClassroom = this.onChangeClassroom.bind(this);
        this.onSelectClassroom = this.onSelectClassroom.bind(this);
        this.onJoinClassroom = this.onJoinClassroom.bind(this);

        this.state = {
            classrooms: [],
            classroom_new_id: '',
            classroom_id: '',

            show: props.page ? props.page : 'default',

            successful: false,
            message: '',
        };
    }

    componentDidMount () {
        const email = this.props.user.email;
        const student_data = BackendService.getStudentData( email );
        this.setState(student_data);
    }

    onChangeClassroom (e) {
        this.setState({
            classroom_new_id: e.target.value.toUpperCase(),
            classroom_id: ''
        });
    }

    onSelectClassroom (e) {
        this.setState({
            classroom_id: e.target.value 
        });
    }

    onJoinClassroom () {
        let classrooms = this.state.classrooms;
        let classroom = '';
        let id = '';

        // check if classroom entered is already retrieved
        id = this.state.classroom_new_id.length > 0 ? this.state.classroom_new_id : this.state.classroom_id;
        classrooms.forEach(e => {
            classroom = e.id === id ? e : classroom
        });

        // not found, retrieve it
        if (!classroom) {
            classroom = BackendService.getClassroomData(id);
            classrooms.push(classroom);
        }

        // show classroom        
        this.setState({
            show: 'classroom',
            classrooms: classrooms,
            classroom: classroom
        });
    }

    render() {
        const classrooms = this.state.classrooms;

        return (
            this.state.show === 'classroom' &&
            (<Classroom classroom={this.state.classroom}/>)
            ||
            (<div>
                <Form
                    onSubmit={this.handleRegister}
                    ref={c => {
                    this.form = c;
                    }}
                >
                    {!this.state.successful && (
                    <div>
                        <div className="form-group mt-2">
                        <Input
                            type="text"
                            className="form-control"
                            placeholder={_t('new_classroom_id')}
                            name="classroom_new_id"
                            value={this.state.classroom_new_id}
                            onChange={this.onChangeClassroom}
                            maxLength="4"
                            validations={[]}
                        />
                        </div>

                        <div className="form-group mt-2 row">
                            <div className="col-12 text-center">
                                <Select
                                    name='classroom_id' 
                                    value={this.state.classroom_id} 
                                    className='form-select' 
                                    validations={[]} 
                                    disabled={this.state.classroom_new_id.length > 0}
                                    onChange={this.onSelectClassroom}
                                >
                                    <option value='' className='text-muted'>{_t('select_previous')}</option>
                                    {
                                        this.state.classrooms.map((c,i) => 
                                            <option key={i} value={c.id}>{c.title}</option>
                                        )
                                    }
                                </Select>
                            </div>
                        </div>

                        <div className="form-group mt-2 row">
                            <div className="col-12 text-center">
                                <button className="btn btn-success btn-block" onClick={this.onJoinClassroom}>{_t('join')}</button>
                            </div>                                    
                        </div>
                    </div>
                    )}
                    {this.state.message && (
                    <div className="form-group">
                        <div
                        className={
                            this.state.successful
                            ? "alert alert-success"
                            : "alert alert-danger"
                        }
                        role="alert"
                        >
                        {this.state.message}
                        </div>
                    </div>
                    )}
                    <CheckButton
                        style={{ display: "none" }}
                        ref={c => {
                        this.checkBtn = c;
                        }}
                    />
                </Form>
            </div>)                    
        );
    }
}

export default Student;