import React from "react";
import _t from "../../services/i18n.service";

function QuestionCard (props) {
    let question = props.question;

    return (
        <div className="card row mt-4">
            <div className="card-header">
                <div className="row">
                    <div className="col-6">{question.title}</div> 
                    <div className="col-6 d-flex justify-content-end">
                        {question.state === 'editable' && (<a href="#" className="bi bi-pencil-square text-primary" onClick={() => props.onEdit(question)}> {_t('edit')}</a>)}
                        {question.state === 'pending' && (<a href="#" className="bi bi-hourglass-split text-primary" onClick={() => props.onEdit(question)}> {_t('pending')}</a>)}
                        {question.state === 'answered' && (<a href="#" className="bi bi-check2-circle"> {_t('answered')}</a>)}
                        {question.state === 'unanswered' && (<a href="#" className="bi bi-x-circle"> {_t('unanswered')}</a>)}
                    </div>
                </div>
            </div>
            <div className="card-body">
                <div className="row">
                    <div className="col-12">
                        <span className="d-flex justify-content-start">{question.date}</span>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default QuestionCard;