import React, { Component } from "react"
import Form from "react-validation/build/form";
import Input from "react-validation/build/input";
import CheckButton from "react-validation/build/button";
import BackendService from "../../services/backend.service"
import _t from "../../services/i18n.service";
import { useNavigate } from "react-router-dom";

const required = value => {
    if (!value || value.length == 0) {
      return (
        <div className="alert alert-danger" role="alert">
          {_t('field_required')}
        </div>
      );
    }
  };

class Question extends Component {

    constructor(props) {
        super(props);

        this.onSubmit = this.onSubmit.bind(this);
        this.onChange = this.onChange.bind(this);

        this.state = {
            level: -1,

            successful: false,
            message: '',
        }
    }

    onChange(e) {
        const level = parseInt(e.target.value)
        this.setState({
          level: level
        });
    }

    onSubmit(e) {
        e.preventDefault();
        this.setState({
          message: "",
          successful: false
        });
        this.form.validateAll();
        this.props.onSubmit(this.state.level, this.props.question.id);
    }

 
    render() {
        const levels = [...Array(10).keys()].map(e => e+1); // an array with a range of values from 1 to 10

        return (
            <div>
                <div className="center-form">
                    <div>
                        <h5>{this.props.question.title}</h5>
                        
                        <Form
                            onSubmit={this.onSubmit}
                            ref={c => {
                                this.form = c;
                            }}
                        >
                            <div className="row">
                                <div className='col-6 d-flex justify-content-end'>
                                    {!this.state.successful && (
                                    <div className="d-flex flex-column">                                
                                        {levels.map((l, i) => 
                                            <div  className="p-1" key={i}>
                                            <input
                                            key={i}
                                            type="radio"
                                            id={'level_'+l}
                                            name="level"
                                            value={l}
                                            onChange={this.onChange}
                                            validations={[required]}
                                            /> {l}
                                            </div>
                                        )}
                                    </div>
                                    )}
                                </div>
                                <div className='col-6  d-flex justify-content-start'>
                                    <img src="/stairs.jpg" width="200"/>
                                </div>
                            </div>

                            <div className="form-group mt-5 row">
                                <div className="col-12 text-center">
                                    <button className="btn btn-success btn-block" onClick={this.onSubmit}>{_t('submit')}</button>
                                </div>
                            </div>
      
                            
                            {this.state.message && (
                            <div className="form-group">
                                <div
                                className={
                                    this.state.successful
                                    ? "alert alert-success"
                                    : "alert alert-danger"
                                }
                                role="alert"
                                >
                                {this.state.message}
                                </div>
                            </div>
                            )}
                            <CheckButton
                                style={{ display: "none" }}
                                ref={c => {
                                this.checkBtn = c;
                                }}
                            />
                        </Form>
                    </div>
                </div>
            </div>
        );    
    }
} 

export default Question;