import React, { Component } from "react";
import { BrowserRouter as Router, Routes, Route, Link, Navigate } from "react-router-dom";
import { Container, Navbar, Nav, NavDropdown } from 'react-bootstrap';
import {LinkContainer} from 'react-router-bootstrap';

import AuthService from "./services/auth.service";

import ACL from "./services/acl.service";
import Unauthorized from "./components/unauthorized.component";
import Login from "./components/login.component";
import Register from "./components/register.component";
import Language from "./components/language.component";
import _t from "./services/i18n.service";

import Professor from "./components/professor/professor.component";
import Student from "./components/student/student.component";
import Stats from "./components/student/stats.component";
import DelUser from "./components/student/del_user.component";
import backendService from "./services/backend.service";


class App extends Component {

  constructor(props) {

    super(props);
    this.logOut = this.logOut.bind(this);
    this.logIn = this.logIn.bind(this);
    this.onChangeLang = this.onChangeLang.bind(this);
    this.delUser = this.delUser.bind(this);

    const user = AuthService.getCurrentUser();
    this.state = {user: user ? user : {lang: 'en', email: null}};
    window.$lang = this.state.user.lang;
  }

  logIn() {
    const user = AuthService.getCurrentUser();
    this.setState({user: user});
  }
  
  logOut() {
    AuthService.logout();
    this.setState({user: AuthService.getCurrentUser()});
  }

  onChangeLang(lang) {
    window.$lang = lang;
    this.setState({user: {...this.state.user, lang: lang}});
    localStorage.setItem('user', JSON.stringify(this.state.user));
    // TODO: update language on server 
  }

  delUser(successful) {
    if (successful)  {
      this.logOut();
    }
  }

  componentDidMount() {
    this.setState({user: AuthService.getCurrentUser()});
  }

  componentDidUpdate() {
    window.$lang = this.state.user.lang;
  }

  render() {    
    const user = this.state.user;

    return (
      <Router>
      <>
        <Navbar bg="light" expand="lg"> 
          <Container>
            <Navbar.Brand>
              <Link to={"/"}>
                &nbsp; levelUp 
              </Link>
            </Navbar.Brand>
            <Navbar.Toggle aria-controls="basic-navbar-nav" />
            <Navbar.Collapse id="basic-navbar-nav">
              <Nav className="me-auto">
                {user && user.role === 'ROLE_PROFESSOR' && (

                    <LinkContainer to={"/prof"} reloadDocument>
                      <Nav.Link>
                        <i className="bi bi-easel"></i> {_t('classrooms_title')}
                      </Nav.Link>                    
                    </LinkContainer>
                )}

                {user && user.role === 'ROLE_STUDENT' && (
                  <>
                    <LinkContainer to={"/stu"} reloadDocument>
                      <Nav.Link> 
                        <i className="bi bi-easel"></i> {_t('classrooms_title')}
                      </Nav.Link>
                    </LinkContainer>

                    <LinkContainer to={"/stu-stats"}>
                      <Nav.Link>
                        <i className="bi bi-clipboard-data"></i> {_t('stats_title')}
                      </Nav.Link>
                    </LinkContainer>
                  </>
                )}
                <NavDropdown.Divider />
                {user && user.email && (
                  <>
                    <LinkContainer to={"/lang"}>
                      <Nav.Link>
                        <i className="bi bi-translate"></i> {_t('link_language')}
                      </Nav.Link>
                    </LinkContainer>

                    <LinkContainer to={"/del-user"}>
                      <Nav.Link>
                        <i className="bi bi-trash"></i> {_t('delete_user')}
                      </Nav.Link>
                    </LinkContainer>

                    <Nav.Link href="/logout" onClick={this.logOut}>
                      <i className="bi bi-box-arrow-left"></i> {_t('link_logout')}
                    </Nav.Link>
                  </>
                ) || (
                    <>
                      <LinkContainer to={"/login"}>
                        <Nav.Link>
                          <i className="bi bi-box-arrow-in-right"></i> {_t('link_login')}
                        </Nav.Link>
                      </LinkContainer>

                      <LinkContainer to={"/register"}>
                        <Nav.Link>
                          <i className="bi bi-person-plus"></i> {_t('link_register')}
                        </Nav.Link>
                      </LinkContainer>
                    </>
                )}              
              </Nav>
            </Navbar.Collapse>
          </Container>
        </Navbar>



        <div className="container mt-3">
          <Routes>
            <Route exact path="/" element={
              <ACL roles={['ROLE_PROFESSOR', 'ROLE_STUDENT']}>
                {this.state.user.role === 'ROLE_PROFESSOR' ? <Navigate to="/prof" replace /> : <Navigate to="/stu" replace />}
              </ACL>
            } />            
            <Route exact path="/login" element={<Login cb={this.logIn} onChangeLang={this.onChangeLang}/>} />
            <Route exact path="/unauthorized" element={<Unauthorized />} />
            <Route exact path="/register" element={<Register user={this.state.user} onChangeLang={this.onChangeLang}/>} />
            <Route exact path="/lang" element={<Language onClick={this.onChangeLang}/>} />
            
            <Route exact path="/prof" element={
              <ACL roles={['ROLE_PROFESSOR']}>
                <Professor user={this.state.user}/>
              </ACL> 
            } />
            
            <Route exact path="/stu" element={
              <ACL roles={['ROLE_STUDENT']}>
                <Student user={this.state.user}/>
              </ACL>    
            } />

            <Route exact path="/stu-stats" element={
              <ACL roles={['ROLE_STUDENT']}>
                <Stats user={this.state.user}/>
              </ACL>    
            } />

            <Route exact path="/del-user" element={
              <ACL roles={['ROLE_STUDENT']}>
                <DelUser user={this.state.user} delUser={this.delUser}/>
              </ACL>    
            } />
            <Route path="*" element={<Navigate to="/" replace />} />
          </Routes>
        </div>
      </>
      </Router>
    );
  }
}

export default App;