import React, { Component } from "react"
import Form from "react-validation/build/form";
import Input from "react-validation/build/input";
import CheckButton from "react-validation/build/button";
import BackendService from "../../services/backend.service"
import _t from "../../services/i18n.service";

const required = value => {
    if (!value) {
      return (
        <div className="alert alert-danger" role="alert">
          {_t('field_required')}
        </div>
      );
    }
  };

class NewClassroom extends Component {

    constructor(props) {
        super(props);

        this.handleCreate = this.handleCreate.bind(this);
        this.onChangeTitle = this.onChangeTitle.bind(this);

        this.state = {
            title: '',

            successful: false,
            message: '',
        }
    }

    onChangeTitle(e) {
        this.setState({
          title: e.target.value
        });
      }

    handleCreate(e) {

        e.preventDefault();
        this.setState({
          message: "",
          successful: false
        });
        this.form.validateAll();    

        if (this.checkBtn.context._errors.length === 0) {
            BackendService.newClassroom(
                this.state.title    
            ).then(
                response => {
                    this.setState({
                        message: response.message,
                        successful: true
                    });
                    setTimeout(() => this.props.onNewClassroom(response.classroom), 1000);
                },
                error => {
                    const resMessage =
                        (error.response &&
                        error.response.data &&
                        error.response.data.message) ||
                        error.message ||
                        error.toString();
                    this.setState({
                        successful: false,
                        message: resMessage
                });
            }
        );
        }

    }

    render() {
        return (
            <div>
                <div className="center-form">
                    <div className="">
                        <h5>{_t('new_classroom_title')}</h5>
                        <Form
                            onSubmit={this.handleRegister}
                            ref={c => {
                            this.form = c;
                            }}
                        >
                            {!this.state.successful && (
                            <div>
                                <div className="form-group mt-2">
                                <label htmlFor="title">{_t('title')}</label>
                                <Input
                                    type="text"
                                    className="form-control"
                                    name="title"
                                    value={this.state.title}
                                    onChange={this.onChangeTitle}
                                    validations={[required]}
                                />
                                </div>
                                <div className="form-group mt-2 row">
                                    <div className="col-6 text-center">
                                        <button className="btn btn-secondary btn-block" onClick={this.props.onCancelNewClassroom}>{_t('cancel')}</button>
                                    </div>
                                    <div className="col-6 text-center">
                                        <button className="btn btn-success btn-block" onClick={this.handleCreate}>{_t('create')}</button>
                                    </div>                                    
                                </div>
                            </div>
                            )}
                            {this.state.message && (
                            <div className="form-group">
                                <div
                                className={
                                    this.state.successful
                                    ? "alert alert-success"
                                    : "alert alert-danger"
                                }
                                role="alert"
                                >
                                {this.state.message}
                                </div>
                            </div>
                            )}
                            <CheckButton
                                style={{ display: "none" }}
                                ref={c => {
                                this.checkBtn = c;
                                }}
                            />
                        </Form>
                    </div>
                </div>
            </div>
        );    
    }
} 

export default NewClassroom;