import React, { Component } from "react";
import _t from "../../services/i18n.service";
import Question from './question.component';
import QuestionCard from './question_card.component';
import BackendService from "../../services/backend.service";

class Classroom extends Component  {

    constructor(props) {
        super(props);
        this.onEdit = this.onEdit.bind(this);
        this.onSubmit = this.onSubmit.bind(this);

        this.state = {
            show: 'classroom',
            question: null
        };
    }

    onEdit (q) {
        this.setState({
            show: 'question',
            question: q
        })
    }

    onSubmit (level, question) {
        BackendService.answerQuestion(level, question).then(
            result => {
                
            },
            error => {}
        );
    }

    render () {
        const classroom = this.props.classroom;

        return (
            <div>
            {(this.state.show === 'classroom') &&
            (<div>
                <h4>{classroom.title}</h4>
                <h5>{_t('questions')}:</h5>
                {classroom.questions.map((q,i) =>
                    <QuestionCard key={i} question={q} onEdit={this.onEdit}/>
                )}
            </div>
            )}
            {(this.state.show === 'question') &&
            (
                <Question onSubmit={this.onSubmit} question={this.state.question} />
            )}
            </div>
        );
    }
}

export default Classroom;