import React from "react"

export default function ClassroomCard (props) {
    let classroom = props.classroom;    

    return (
        <div className="card row mt-4">
            <div className="card-header">
                <div className="row">
                    <div className="col-6">{classroom.id}</div> 
                    <div className="col-6 d-flex justify-content-end"><i className="bi bi-person-fill"></i> {classroom.num_users}</div>                             
                </div>
            </div>
            <div className="card-body">
                <div className="row">
                    <div className="col-10">
                        <h5 className="card-title d-flex justify-content-start">{classroom.title}</h5>
                    </div>
                    <div className="col-2 d-flex justify-content-end">
                        <span className="bi bi-pencil-square text-primary" onClick={() => props.goToClassroom(classroom)}></span>
                    </div>
                </div>
            </div>
        </div>
    );
} 