import React from "react"
import { BarChart, Bar, Cell, XAxis, YAxis, CartesianGrid, Tooltip, Legend, ResponsiveContainer } from 'recharts';
import _t from "../../services/i18n.service"


function Stats () {

  const data = [
    {
      name: 'self regulation',
      min: 3,
      avg: 5,
      max: 10
    },
    {
      name: 'setting goals',
      min: 2,
      avg: 3,
      max: 4
    }, 
    {
      name: 'knowing your values',
      min: 7,
      avg: 8,
      max: 10
    },
  ];

  return (

    <BarChart
      width={600}
      height={300}
      data={data} 
      margin={{
        top: 5,
        right: 30,
        left: 20,
        bottom: 5,
      }}
    >
      <CartesianGrid strokeDasharray="3 3" />
      <XAxis dataKey="name" />
      <YAxis />
      <Tooltip />
      <Legend />
      <Bar dataKey="min" fill="#e9c46a" />
      <Bar dataKey="avg" fill="#f4a261" />
      <Bar dataKey="max" fill="#e76f51" />
    </BarChart>

  );
}


export default Stats;