import React, { Component } from "react"
import Form from "react-validation/build/form";
import Input from "react-validation/build/input";
import Select from "react-validation/build/select";
import CheckButton from "react-validation/build/button";
import BackendService from "../../services/backend.service"
import { Routes, Route, Link, Navigate } from "react-router-dom";
import _t from "../../services/i18n.service";
import { useNavigate } from "react-router-dom";

const required = value => {
    if (!value || value.length == 0) {
      return (
        <div className="alert alert-danger" role="alert">
          {_t('field_required')}
        </div>
      );
    }
  };

class NewQuestion extends Component {

    constructor(props) {
        super(props);

        this.handleCreate = this.handleCreate.bind(this);
        this.onSelectQuestion = this.onSelectQuestion.bind(this);
        this.onSetPhase = this.onSetPhase.bind(this);
        this.onSetPrePost = this.onSetPrePost.bind(this);
        this.onSetPrompt = this.onSetPrompt.bind(this);

        this.state = {
            questionType: '',
            phase: 0,
            prepost: '',
            prompt: 'none',

            successful: false,
            message: '',
        }
    }

    onSelectQuestion(e) {
        this.setState({
          questionType: e.target.value
        });
      }

    onSetPhase(e) {
        const phase = parseInt(e.target.value);
        this.setState({
            phase: phase,
            prepost: phase === 1 ? '' : this.state.prepost,
        });
    }

    onSetPrePost(e) {
        this.setState({
            prepost: e.target.value
        });
    }

    onSetPrompt(e) {
        this.setState({
            prompt: e.target.value
        });
    }

    handleCreate(e) {

        e.preventDefault();
        this.setState({
          message: "",
          successful: false
        });
        this.form.validateAll();    

        if (this.checkBtn.context._errors.length === 0) {
            BackendService.newQuestion(
                this.props.classroom.id,
                this.state.questionType,
                this.state.phase,
                this.state.prepost,
                this.state.prompt,
            ).then(
                response => {
                    this.setState({
                        message: response.message,
                        successful: true
                    });
                    setTimeout(() => this.props.onNewQuestion(response.question), 1000);
                },
                error => {
                    const resMessage =
                        (error.response &&
                        error.response.data &&
                        error.response.data.message) ||
                        error.message ||
                        error.toString();
                    this.setState({
                        successful: false,
                        message: resMessage
                    });
                }
            );
        }
    }

    render() {
        console.log("render NewQuestion");
        return (
            <div>
                <div className="center-form">
                    <div className="">
                        <h5>{_t('new_question_title')}</h5>
                        <Form
                            onSubmit={this.handleCreate}
                            ref={c => {
                            this.form = c;
                            }}
                        >
                            {!this.state.successful && (
                            <div>
                                <div className="form-group mt-2 row">
                                    <div className="col-12 text-center">
                                        <Select name='questionType' value='' className='form-select' validations={[required]}>
                                            <option value='' className='text-muted'>{_t('choose_question')}</option>
                                            <option value='1'>{_t('question_type_1')}</option>
                                            <option value='2'>{_t('question_type_2')}</option>
                                            <option value='3'>{_t('question_type_3')}</option>
                                        </Select>
                                    </div>
                                </div>

                                <div className="form-group mt-2 row">
                                    <label htmlFor="phase 1" className="col-6 text-center">{_t('phase')} I</label>
                                    <label htmlFor="phase 2" className="col-6 text-center">{_t('phase')} II</label>
                                </div>
                                <div className="row">

                                    <input
                                    type="radio"
                                    id="phase 1"
                                    className="col-6"
                                    name="phase"
                                    value="1"
                                    onChange={this.onSetPhase}
                                    defaultChecked
                                    /> 

                                    <input
                                    type="radio"
                                    id="phase 2"
                                    className="col-6"
                                    name="phase"
                                    value="2"
                                    onChange={this.onSetPhase}
                                    /> 
                                
                                </div>

                                {this.state.phase === 2 && (
                                <div>
                                    <div className="form-group mt-2 row">
                                        <label htmlFor="pre" className="col-6 text-center">Pre</label>
                                        <label htmlFor="post" className="col-6 text-center">Post</label>
                                    </div>
                                    <div className="row">

                                        <input
                                        type="radio"
                                        id="pre"
                                        className="col-6"
                                        name="prepost"
                                        value="1"
                                        defaultChecked
                                        onChange={this.onsetPhase}
                                        /> 

                                        <input
                                        type="radio"
                                        id="post"
                                        className="col-6"
                                        name="prepost"
                                        value="post"
                                        onChange={this.onSetPrePost}
                                        /> 
                                    
                                    </div>
                                </div>)}

                                <div className="form-group mt-2 row">
                                    <div className="col-12 text-center">
                                        <Select name='prompt' value='none' className='form-select' validations={[required]}>
                                            <option value='none'>{_t('none')}</option>
                                            <option value='opd'>{_t('once_per_day')}</option>
                                            <option value='opw'>{_t('once_per_week')}</option>
                                            <option value='opm'>{_t('once_per_month')}</option>
                                        </Select>
                                    </div>
                                </div>                                

                                <div className="form-group mt-5 row">
                                    <div className="col-6 text-center">
                                        <button className="btn btn-secondary btn-block" onClick={this.props.onCancelNewQuestion}>{_t('cancel')}</button>
                                    </div>
                                    <div className="col-6 text-center">
                                        <button className="btn btn-success btn-block" onClick={this.handleCreate}>{_t('create')}</button>
                                    </div>
                                </div>
                            </div>
                            )}
                            {this.state.message && (
                            <div className="form-group">
                                <div
                                className={
                                    this.state.successful
                                    ? "alert alert-success"
                                    : "alert alert-danger"
                                }
                                role="alert"
                                >
                                {this.state.message}
                                </div>
                            </div>
                            )}
                            <CheckButton
                                style={{ display: "none" }}
                                ref={c => {
                                this.checkBtn = c;
                                }}
                            />
                        </Form>
                    </div>
                </div>
            </div>
        );    
    }
} 

export default NewQuestion;