import React from "react";
import _t from "../../services/i18n.service";

function QuestionCard (props) {
    let question = props.question;

    return (
        <div className="card row mt-4">
            <div className="card-header">
                <div className="row">
                    <div className="col-6">{question.title}</div> 
                    <div className="col-4 d-flex justify-content-end">
                        <i className="bi bi-person-fill text-secondary"></i> {question.num_answers}
                    </div>
                    <div className="col-2 d-flex justify-content-end">
                        <i className="bi bi-trash text-primary" onClick={() => props.onDelete(question)}></i>
                    </div>
                </div>
            </div>
            <div className="card-body">
                <div className="row">
                    <div className="col-6">
                        <h5 className="card-title d-flex justify-content-start">{question.date}</h5>
                    </div>
                    <div className="col-6">
                        <i className="d-flex justify-content-end">{_t('phase')} {question.phase} {question.pre_post}</i>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default QuestionCard;